
import React, { useEffect, useState } from 'react'

//

// import cardFormatter from './cardFormatter/cardFormatter'



function useInput({
    className, type, inputMode, inputEvent,
    bankCurrencyActiveType, bankWithdrawalValue
}) {

    //

    const [value, setValue] = useState(
        inputEvent === 'blockchainCurrency'
            ? '2'
            : inputEvent === 'bankCurrency' ? '2' : ''
    );
    const [error, setError] = useState(null);

    //

    useEffect(
        () => {
            if     (inputEvent === 'blockchainCurrency'   && value < 1)    setError("Min 1 usdt");
            else if(inputEvent === 'bankCurrency' && value < 1) setError("Min 1 usdt");
            else setError(null);
        }, [value]
    )

    console.log('bankWithdrawalValue', bankWithdrawalValue)
    
    //

    const input =
        <input
            className={className}
            value={inputEvent === 'bankCurrency' ? bankWithdrawalValue : value}
            // maxLength={+assetDataOneReserves?.decimals}
            onChange={ e => setValue( e.target.value ) }
            type={type}
            name='userNumber'
            inputMode={inputMode}
        />



    //

    return [
        input,
        error
    ];

}



export default useInput