import React, { useState, useEffect } from 'react'
//
import { ReactComponent as Tether_usdt_trc20_SVG } from "~IMG/svg/icon_coins/tether_usdt_trc20.svg"
import { ReactComponent as Tether_usdt_erc20_SVG } from "~IMG/svg/icon_coins/tether_usdt_erc20.svg"

import { ReactComponent as Arrow_SVG } from "~IMG/svg/arrow_nav/arrow.svg"

import { ReactComponent as USD_SVG } from "~IMG/svg/icon_currency/usd.svg"
import { ReactComponent as EUR_SVG } from "~IMG/svg/icon_currency/eur.svg"
import { ReactComponent as CNY_SVG } from "~IMG/svg/icon_currency/cny.svg"

//

const useSelect = ({
    eventType
}) => {

    //

    const [paymentEvent, setPaymentEvent] = useState( 'Tether USDT (ERC20)' );
    const [bankCurrencyActiveType, setBankCurrencyActiveType] = useState( 'USD' );


    const [paymentEventDropDownOpen, setPaymentEventDropDownOpen] = useState( false );

    //

    // useEffect(
    //     () => {

    //     }, []
    // )
    
    //

    let selectIconPayment = paymentEvent === 'Tether USDT (ERC20)' ? <Tether_usdt_trc20_SVG /> : <Tether_usdt_erc20_SVG />
    let selectIconActiveCurrency = bankCurrencyActiveType === 'USD' 
        ? <USD_SVG /> 
        : bankCurrencyActiveType === 'EUR' ? <EUR_SVG /> : <CNY_SVG />
    let bankCurrencyActiveTypeFormated = bankCurrencyActiveType === 'USD' 
        ? 'USDUSD'
        : bankCurrencyActiveType === 'EUR' ? 'USDEUR' : 'USDCNY'

    console.log('bankCurrencyActiveTypeFormated2312312321', bankCurrencyActiveTypeFormated)
    


    const changeOption = (value) => {
        eventType === 'payment' && setPaymentEvent(value)  
        eventType === 'bank' && setBankCurrencyActiveType(value)
        setPaymentEventDropDownOpen(!paymentEventDropDownOpen)
    }

    //

    const selectPayment = <>
        <div className="btn_wrap_1">

            <div onClick={() => setPaymentEventDropDownOpen(!paymentEventDropDownOpen)} className="btn btn_active">
                <div className=""> {selectIconPayment} </div>
                <span> {paymentEvent} </span>
                <div className="btn_arrow"> <Arrow_SVG /> </div>
            </div>

            {paymentEventDropDownOpen && 
                <div className="dropdown">
                    <div className="dropdown_inner">

                        <div onClick={() => changeOption('Tether USDT (TRC20)')} className="option">
                            <div className="option_svg"> <Tether_usdt_trc20_SVG /> </div>
                            <span>Tether USDT (TRC20)</span>
                        </div>

                        <div onClick={() => changeOption('Tether USDT (ERC20)')} className="option">
                            <div className="option_svg"> <Tether_usdt_erc20_SVG /> </div>
                            <span>Tether USDT (ERC20)</span>
                        </div>

                    </div>
                </div>
            }

        </div>
    </>

    const selectBank = <>
        <div className="btn_wrap_1">

            <div onClick={() => setPaymentEventDropDownOpen(!paymentEventDropDownOpen)} className="btn btn_active_2">
                <span> {bankCurrencyActiveType} </span>
                <div className="btn_currency"> {selectIconActiveCurrency} </div>
            </div>

            {paymentEventDropDownOpen && 
                <div className="dropdown">
                    <div className="dropdown_inner">

                        <div onClick={() => changeOption('CNY')} className="option_2">
                            <span>CNY</span>
                            <div className="option_svg"> <CNY_SVG /> </div>
                        </div>

                        <div onClick={() => changeOption('EUR')} className="option_2">
                            <span>EUR</span>
                            <div className="option_svg"> <EUR_SVG /> </div>
                        </div>

                        <div onClick={() => changeOption('USD')} className="option_2">
                            <span>USD</span>
                            <div className="option_svg"> <USD_SVG /> </div>
                        </div>

                    </div>
                </div>
            }

        </div>
    </>

    return [
        eventType === 'payment' && selectPayment,
        eventType === 'bank'    && selectBank,
        paymentEvent,
        bankCurrencyActiveTypeFormated
    ]

}

export default useSelect